import './login.css';

const Addsale = () => {
    return (
        <div className="Addsale">
            <div className='heading text-center mb-5 mt-5'>
                <h1>ADD SALE ENTRY</h1>
            </div>
            <div className="form">
                <form>
                    <div className="form-floating mb-3">
                        <input type="email" className="form-control" id="floatingInput2" placeholder="Product Name" />
                        <label htmlFor="floatingInput2">Product Name</label>
                    </div>
                    <div className="form-floating mb-3">
                        <input type="email" className="form-control" id="floatingInput3" placeholder="Quantity" />
                        <label htmlFor="floatingInput3">Quantity</label>
                    </div>

                    <div className="form-floating mb-3">
                        <input type="email" className="form-control" id="floatingInput4" placeholder="Amount" />
                        <label htmlFor="floatingInput4">Amount</label>
                    </div>
                    <button type="submit" className="btn custom-btn btn-primary">Submit</button>
                </form>
            </div>
        </div>
    )
}

export default Addsale;